.form{
  margin-top: 30px;
  margin-bottom: 30px;
}

.label{
   display: block;
   margin-top: 10px;
   font-size: 15px;
   font-weight: 600;
}
.input {
   display: block;
   margin-left: auto;
   margin-right: auto;
   width: 100%;
   font-size: 15px;
   padding: 10px;
   margin-top: 10px;
}

.btn{
   border-radius: 1em;
   margin-top: 20px;
   border: none;
   outline: none;
   cursor: pointer;
   background-color: blue;
   color: white;
   font-size: 20px;
   font-weight: 600;
   box-shadow: 0 11px 7px rgba(0, 0, 0, .15);
   padding: 10px;
   transition: all 400ms ease;
}

.btn:hover{
   box-shadow: 0 11px 7px rgba(60, 215, 158, 0.15);
   background: green;
}